<template>
  <div>
    <el-card>
      <div slot="header">
        基本信息
        <el-button
          style="float: right"
          type="info"
          size="small"
          @click="$router.go(-1)"
          >返回</el-button
        >
      </div>
      <div>
        <!-- :rules="rules" -->
        <el-form
          :model="Form"
          ref="formRef"
          :rules="rule"
          label-width="170px"
          class="demo-ruleForm"
        >
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="负责人" prop="peoId" required="">
                <el-select
                  v-model="Form.peoId"
                  filterable
                  placeholder="请输入内容"
                >
                  <el-option
                    v-for="item in Options"
                    :key="item.id"
                    :label="item.teaName"
                    :value="item.teaId"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item label="学科门类" prop="subjectCategory">
                <el-input v-model="Form.subjectCategory"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="项目名称" prop="tpName">
                <el-input v-model="Form.tpName"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="项目编号" prop="tpNumber">
                <el-input v-model="Form.tpNumber"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="负责人类型" prop="principalType">
                <el-input v-model="Form.principalType"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="12">
              <el-form-item v-if="this.istea == 1" label="所属单位" prop="crBelongDept">
                <el-input v-model="Form.projectSourceUnit"></el-input>
              </el-form-item>
              <el-form-item v-else label="所属学院" prop="crBelongDept">
                <el-input v-model="Form.projectSourceUnit"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="项目级别" prop="projectLevel">
                <el-input v-model="Form.projectLevel"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="项目分类" prop="peojectType">
                <el-input v-model="Form.peojectType"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="结项编号" prop="closingNumber">
                <el-input v-model="Form.closingNumber"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="批准号" prop="approvalNumber">
                <el-input v-model="Form.approvalNumber"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="项目来源单位" prop="projectSourceUnit">
                <el-input v-model="Form.projectSourceUnit"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="承担单位排名" prop="undertakingRank">
                <el-input v-model="Form.undertakingRank"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="项目状态" prop="projectStatus">
                <el-input v-model="Form.projectStatus"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="立项日期" prop="projectDate">
                <el-input v-model="Form.projectDate"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="项目开始时间" prop="projectStartTime">
                <el-input v-model="Form.projectStartTime"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="计划完成日期" prop="planFinishDate">
                <el-input v-model="Form.planFinishDate"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="成果形式" prop="resultForm">
                <el-input v-model="Form.resultForm"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="合同类型" prop="contractType">
                <el-input v-model="Form.contractType"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="合同经费" prop="contractFunding">
                <el-input v-model="Form.contractFunding"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="财务账号" prop="financialAccount">
                <el-input v-model="Form.financialAccount"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item
                label="配套经费（单位:万元）"
                prop="supportingExpenses"
              >
                <el-input v-model="Form.supportingExpenses"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="自筹经费（单位:万元）" prop="seltFinancing">
                <el-input v-model="Form.seltFinancing"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row :gutter="10">
            <el-col :span="12">
              <el-form-item label="专项经费（单位:万元）" prop="specialFunds">
                <el-input v-model="Form.specialFunds"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="备注" prop="remarksInfo">
                <el-input v-model="Form.remarksInfo"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

        </el-form>
      </div>
    </el-card>

    <el-card>
      <div slot="header">作者信息
        <span>
          <el-tooltip 
                effect="dark"
                content="新增"
                placement="right"
                :enterable="false"
              >
                
                <el-button
                  class="add"
                  type="primary"
                  icon="el-icon-plus"
              v-if="is_authority.is_user_add"
                  size="mini"
                  @click="authAdd"
                ></el-button>
          </el-tooltip>
        </span>
      </div>
      <div>
        <el-table :data="authForm" border stripe>

          <el-table-column label="作者类型" align="center">
            <template slot-scope="scope">
              <el-select v-model="scope.row.authorType">
                <el-option
                  label="本校老师"
                  value="本校老师">
                </el-option>
                <el-option
                  label="本校学生"
                  value="本校学生">
                </el-option>
                <el-option
                  label="外校人员"
                  value="外校人员">
                </el-option>
              </el-select>
            </template>
          </el-table-column>

          <el-table-column prop="authorName" label="* 作者姓名" align="center">
            <template slot-scope="scope">
              <el-input v-model="authForm[scope.$index].authorName"></el-input>
            </template>
          </el-table-column>

          <el-table-column width="150" prop="authorId" label="作者工号" align="center">
            <template slot-scope="scope">
              <el-input v-model="authForm[scope.$index].authorId"></el-input>
            </template>
          </el-table-column>

          <el-table-column
          width="200"
            prop="firstLevelDisc"
            label="一级学科"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="authForm[scope.$index].firstLevelDisc"></el-input>
            </template>
          </el-table-column>

          <el-table-column width="80" prop="authorSex" label="性别" align="center">
            <template slot-scope="scope">
              <el-input v-model="authForm[scope.$index].authorSex"></el-input>
            </template>
          </el-table-column>

          <el-table-column width="100" prop="signingOrder" label="* 署名顺序" align="center">
            <template slot-scope="scope">
              <el-input v-model="authForm[scope.$index].signingOrder"></el-input>
            </template>
          </el-table-column>

          <el-table-column prop="authorEducation" label="学历" align="center">
            <template slot-scope="scope">
              <el-select v-model="scope.row.authorEducation" placeholder="请选择学历">
                  <el-option
                    label="博士研究生"
                    value="博士研究生">
                  </el-option>
                  <el-option
                    label="硕士研究生"
                    value="硕士研究生">
                  </el-option>
                  <el-option
                    label="本科"
                    value="本科">
                  </el-option>
                  <el-option
                    label="专科"
                    value="专科">
                  </el-option>
                  <el-option
                    label="高中及其以下"
                    value="高中及其以下">
                  </el-option>
                  <el-option
                    label="其他"
                    value="其他">
                  </el-option>
                </el-select>
            </template>
          </el-table-column>

          <el-table-column v-if="this.istea == 1" prop="authorTitle" label="职称" align="center">
            <template slot-scope="scope">
              <el-input v-model="authForm[scope.$index].authorTitle"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            prop="authorWorkUnit  "
            label="工作单位"
            align="center"
          >
            <template slot-scope="scope">
              <el-input v-model="authForm[scope.$index].authorWorkUnit"></el-input>
            </template>
          </el-table-column>

          <el-table-column
            prop="contributionRate"
            label="贡献率：（ ）单位：%"
            align="center"
            ><template slot-scope="scope">
              <el-input
                v-model="authForm[scope.$index].contributionRate"
              ></el-input> </template
          ></el-table-column>

          <el-table-column label="操作" width="120px" align="center">
            <template slot-scope="scope">
              <!-- 删除 -->
              <el-tooltip
                class="item"
                effect="dark"
                content="删除"  v-if="is_authority.is_user_del"
                placement="top-start"
                :enterable="false"
              >
                
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  @click="delRow(scope, authForm)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <el-button class="save" type="primary" @click="submitForm"
        >保存
      </el-button>
    </el-card>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  data() {
    return {
      is_authority: {
        is_user_del: false,
        is_user_upt: false,
        is_user_add: false,
        is_user_add_role: false
      },
      rule:{
        peoId: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        subjectCategory: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        tpName: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        tpNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        principalType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        crBelongDept: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        projectLevel: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        peojectType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        closingNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        approvalNumber: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        projectSourceUnit: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        undertakingRank: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        projectStatus: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        projectDate: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        projectStartTime: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        planFinishDate: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        resultForm: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        contractType: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        contractFunding: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        financialAccount: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        supportingExpenses: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        seltFinancing: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ],
        specialFunds: [
          { required: true, message: '该项不能为空', trigger: 'blur' }
        ]
      },
      authForm: [],
      accessory: [],
      Options: [],
      Form: {},
      List: [],
      istea: undefined,
    }
  },
  created() {
    this.isAuthority()
    this.getDetail()
    this.getTreeselect()
    this.istea = this.$route.query.istea
  },
  computed: {
    ...mapState(['btus']),
  },
  methods: {
    isAuthority() {
      var arr = this.btus.split(',')
      for (var key in this.is_authority) {
        if (arr.indexOf(key) != -1) {
          this.is_authority[key] = true
        }
      }
    },
    authAdd() {
      this.authForm.splice(this.authForm.length,0,{
        authorEducation: "",
        authorId: "",
        authorName: "",
        authorSex: "",
        authorTitle: "",
        authorType: "",
        authorWorkUnit: undefined,
        contributionRate: "",
        firstLevelDisc: "",
        id: "",
        signingOrder: undefined,
        tpId: this.$route.query.id,
      })
    },
    delRow(scope, rows) {
      // console.log(scope);
      if (scope.row.id) {
        const userid = scope.row.id.split(',')
        console.log(userid)
        this.$confirm('确认要删除吗?', '警告', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
          .then(async () => {
            await this.$http.delete(
              '/copyRightInfo/removeByIdCopyRightInfos',
              {
                data: userid
              }
            )
            // console.log(res);
            this.getDetail()
            this.Form = []
          })
          .then(() => {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            })
          })
      } else {
        rows.splice(scope.$index, 1)
      }
    },

    async getTreeselect() {
      this.id = this.$route.query.id
      // console.log(this.$route.query.istea);
      if (this.$route.query.istea == 1) {
        const { data: res } = await this.$http.post(
          '/teaBaseInfo/queryTeaBaseInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        this.Options = res.data.dataList.records
        if (res.code != 20000) return this.$message.error(res.message)
        console.log(this.Options)
        // console.log(this.majorOptions)
      } else if (this.$route.query.istea == 0) {
        const { data: res } = await this.$http.post(
          '/studentInfo/queryStudentInfoList',
          {
            page: {
              current: 1,
              size: 999
            }
          }
        )
        let a = res.data.dataList.records
        a = JSON.parse(JSON.stringify(a).replace(/stuId/g, 'teaId'))
        this.Options = JSON.parse(
          JSON.stringify(a).replace(/stuName/g, 'teaName')
        )
        if (res.code != 20000) return this.$message.error(res.message)
        // console.log(this.Options)
      }
    },

    async getDetail() {
      if (!this.$route.query.id) {
        return
      }
      const id = this.$route.query.id
      // console.log(id)
      const { data: res } = await this.$http.get(
        '/projectInfo/getByIdProjectInfo/' + id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.Form = res.data.entity
      console.log(this.Form)
      this.getAutherinfo()
    },
    async getAutherinfo() {
      const { data: res } = await this.$http.get(
        '/projectAuthorInfo/queryProjectAuthorInfoList/' + this.Form.id
      )
      if (res.code != 20000) return this.$message.error(res.message)
      this.authForm = res.data.dataList
      console.log(this.authForm);
    },
    submitForm() {
      // console.log(this.istea);
      if (this.istea == 1) this.Form.isStu = 1
      else if (this.istea == 0) this.Form.isStu = 0
       this.$refs.formRef.validate(async valid => {
        if (valid) {
          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.authForm[index].authorName  || !this.authForm[index].signingOrder) {
              return this.$message.error('请输入作者姓名和作者署名')
            }

          }
          await this.$http.post(
            '/projectInfo/saveOrUpdateProjectInfo',
            this.Form
          )
          // console.log(this.$route.query.id);
          if (!this.$route.query.id && this.istea == 1) {
              const { data: res } = await this.$http.post(
              '/projectInfo/queryTeaProjectInfoList' ,
               {
                  page: {
                    current: 1,
                    size: 999
                  }
                }
              )
              console.log('tea');
              var tpId = res.data.dataList.records.pop().id
              // console.log(tpId)


          }else if(!this.$route.query.id && this.istea == 0){
            const { data: res } = await this.$http.post(
              '/projectInfo/queryStuProjectInfoList' ,
               {
                  page: {
                    current: 1,
                    size: 999
                  }
                }
              )
              console.log('stu',res);
              var tpId = res.data.dataList.records.pop().id
          }


          for (let index = 0; index < this.authForm.length; index++) {
            if (!this.$route.query.id) {
              this.authForm[index].tpId = tpId
              this.authForm[index].signingOrder = 1
            };
            var { data: res1 } = await this.$http.post(
              '/projectAuthorInfo/saveOrUpdateProjectAuthorInfo',
              this.authForm[index]
            )
          }
              // console.log(authForm[index]);

          if (res1.code != 20000) return this.$message.error(res1.message)
          if (res1.code == 20000) {
            return this.$message.success(res1.message)
            this.getDetail()
            this.$router.go(-1)
          }
        }
      })
    }
  }
}
</script>
<style lang="less" scoped>
.el-card {
  margin-bottom: 20px;
}
.save {
  display: block;
  margin: 20px auto;
  margin-bottom: 0;
  padding: 18px 30px;
}
.add{
  margin-left: 10px;
}
</style>
